<template>
<div class="Revenue-detail">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col span="10" class="font-16">
        <h3>{{$t("common.Revenue_details")}}</h3>
      </Col>
      
      <Col span="14">
        <span>{{$t("common.Date_query")}}</span>
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker> -
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Row type="flex" justify="space-around" class="code-row-bg Account-balance">
    <Col span="12">
      <h2>{{$t("common.Balance_summary")}}</h2>
      <Table highlight-row border :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{width:'100%'}"></Table>
    </Col>
    <Col span="8">
      <!-- <img src="../assets/u7.png" width="425" height="228" alt=""> -->
      <div id="Account_balance" :style="{width: '100%', height: '260px',marginLeft:'20px'}"></div>
    </Col>
  </Row>
  <hr>
  <Row type="flex" justify="space-around" class="code-row-bg balance-detail">
    <Col span="24">
      <h2>{{$t("common.Balance_details")}}</h2>
      <Table highlight-row border :columns="columns2" :data="BalanceData" v-if="ShowTable" ></Table>
      <Table highlight-row border :columns="columns2" :data="BalanceData1" v-if="ShowTableTow" ></Table>
    </Col>
  </Row>
  <!-- <Row type="flex" justify="space-around" class="code-row-bg page-data">
    <Col span="12">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row> -->
</div>
</template>

<script>
  var echarts = require('echarts')
// // 引入基本模板
// let echarts = require('echarts/lib/echarts')
// // 引入提示框和title组件
// require('echarts/lib/component/tooltip')
// // 引入bar图组件
// require('echarts/lib/chart/bar')
// require('echarts/lib/component/title')
// require('echarts/lib/component/legend')
export default {
    name:'Revenue_detail',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            columns1: [
                {
                    title: this.$t("common.Month_fen"),
                    key: 'Month',
                    align:'center',
                    width:90
                },
                {
                    title: this.$t("common.Monthly_income"),
                    key: 'monthly_income',
                    align:'center'
                },
                {
                    title: this.$t("common.Monthly_expenditure"),
                    key: 'monthly_expenditure',
                    align:'center'
                },
                {
                    title: this.$t("common.Monthly_balance"),
                    key: 'monthly_balance',
                    align:'center'
                }
            ],
            columns2: [
                {
                    title: this.$t("common.Date"),
                    key: 'Month',
                    align:'center',
                },
                {
                    title: this.$t("common.Daily_income"),
                    key: 'monthly_income',
                    align:'center'
                },
                {
                    title: this.$t("common.Daily_expenditure"),
                    key: 'monthly_expenditure',
                    align:'center'
                },
                {
                    title: this.$t("common.Daily_balance"),
                    key: 'monthly_balance',
                    align:'center'
                },
                {
                    title: this.$t("common.Remarks"),
                    key: 'remarks',
                    align:'center',
                    render:(h,params) =>{
                      return h('a',{
                        attrs: {
                          href:'javascript:;'
                        },
                        props: {

                        },
                        style: {
                          color:'#00CCFF'
                        }
                      },this.$t("common.Details"))
                    }
                }
            ],
            data1: this.mockTableData1 (),
            BalanceData: this.mockTableData2 (),
            BalanceData1: this.mockTableData2 (),
            Personal_info:null,
            ShowTable:false,
            ShowTableTow:false
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 7; i < 10; i++) {
              data.push({
                  Month:  i+'月',
                  monthly_income: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00',
                  monthly_expenditure: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00',
                  monthly_balance: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00'
              })
          }
          return data;
      },
      mockTableData2 () {
          let data = [];
          for (let i = 7; i < 10; i++) {
              data.push({
                  Month:  this.formatTime (this.getRandomDateBetween()),
                  monthly_income: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00',
                  monthly_expenditure: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00',
                  monthly_balance: '￥'+parseInt(Math.random()*(11-1)+1)+'00.00',
                  remarks:''
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("July  1, 2017 11:13:00"),
          b = + new Date("August  12, 2017 11:13:00");
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '/' + m + '/' + d;
      },
      
      onedata (data,index){
        //console.log(data,index)
        this.Personal_info = data
        //this.sendParams ()
        if(index == 0){
          this.ShowTableTow = false
          this.ShowTable = true
        }else if(index == 1){
          this.ShowTable = false
          this.ShowTableTow = true
        }
      },
      sendParams () {
          this.$router.push({
              path: '/Userinfo',
              name: 'Userinfo',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       },
       drawLine() {
         // 基于准备好的dom，初始化echarts实例
         let Account_balance = echarts.init(document.getElementById('Account_balance'))
         // 绘制图表
         var option_Account_balance = {
               title: {
                   text: this.$t("common.Balance_chart"),
                   x: 'center',
                   y: 'top',
                   textStyle: { //主标题配置
                     fontWeight: 'normal',
                     fontSize: 16,
                   },
                   padding:[20,0,0,0]

               },
               tooltip : {
                   trigger: 'axis'
               },
               color:['#a4d8cc','#78d6ef'],
               legend: {
                  top:'20%',
                  right:'5%',
                  orient: 'horizontal',
                  data:[this.$t("common.Monthly_income"),this.$t("common.Monthly_expenditure"),this.$t("common.Monthly_balance")]
               },
               xAxis: {
                   type: 'category',
                   // boundaryGap: [0, 0],
                   axisPointer: {
                       snap: true,
                       lineStyle: {
                           color: '#004E52',
                           opacity: 0.5,
                           width: 2
                       },
                       label: {
                           show: true,
                           backgroundColor: '#004E52'
                       }
                   },
                   axisLine: {
                     show:true,
                     lineStyle:{
                       color:'#C9C9C9'
                     }
                   },
                   axisTick: {
                     show:false
                   },
                   axisLabel:{
                     textStyle: {
                         color: '#000'
                     }
                   },
                   splitLine: {
                       show: false
                   },
                   data : [this.$t("common.July"),this.$t("common.August"),this.$t("common.September")]
               },
               yAxis: {
                   type: 'value',
                   axisTick: {
                       inside: true,
                       show:false
                   },
                   axisLine: {
                     show:true,
                     lineStyle:{
                       color:'#C9C9C9'
                     }
                   },
                   splitLine: {
                       show: false,
                       lineStyle:{
                         opacity:0.3
                       }
                   },
                   axisLabel: {
                       inside: true,
                       formatter: '{value}\n',
                       align:'right',
                       margin:-10,
                       textStyle: {
                           color: '#000'
                       }
                   },
                   z: 10,
                   max:2000
               },
               grid: {
                   top: 40,
                   left: 45,
                   right: 15,
                   height: 200
               },
               series: [
                 {
                   name: this.$t("common.Monthly_income"),
                   type: 'bar',
                   symbol: 'circle',
                   symbolSize: 10,
                   sampling: 'average',
                   itemStyle: {
                       normal: {
                           color: '#00A4FF'
                       }
                   },
                   data:[300,500,800]
                 },
                 {
                   name: this.$t("common.Monthly_expenditure"),
                   type: 'bar',
                   symbol: 'circle',
                   symbolSize: 10,
                   sampling: 'average',
                   itemStyle: {
                       normal: {
                           color: '#4CB1C3'
                       }
                   },
                   data:[1300,1050,1200]
                 },
                 {
                   name: this.$t("common.Monthly_balance"),
                   type: 'bar',
                   symbol: 'circle',
                   symbolSize: 10,
                   sampling: 'average',
                   itemStyle: {
                       normal: {
                           color: '#E1EDFA'
                       }
                   },
                   data:[780,950,920]
                 }
               ]
             }
        Account_balance.setOption(option_Account_balance);
        function __resizeHanlder(){
          Account_balance.resize();
        }
        setTimeout(function (){
            window.onresize = function () {
              Account_balance.resize();
            }
        },200)
        const sidebarElm = document.getElementsByClassName('content-box')[0]
        sidebarElm.addEventListener('transitionend', __resizeHanlder)
     }
    },
    created(){
         this.handleListApproveHistory();
    },
    mounted() {
      this.drawLine();
    },
}
</script>

<style lang="css">
.Revenue-detail{
  background-color: #fff;
  padding: 20px;
}
.Revenue-detail .search_criteria{
  height: 50px;
  background-color: #fff;
  line-height: 50px;
  border-bottom: 1px solid #eee;
}
.Revenue-detail .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
}
.Revenue-detail .Account-balance{
  padding-top: 20px;
  text-align: left;
  padding-bottom: 30px;
}
.Revenue-detail .Account-balance h2{
  margin-bottom: 10px;
}
.Revenue-detail .balance-detail{
  padding-top: 20px;
  text-align: left;
  padding-bottom: 30px;
  padding: 20px 50px 30px;;
}
.Revenue-detail .balance-detail h2{
  margin-bottom: 10px;
}
</style>



